*{
  font-family: Arial, Helvetica, sans-serif;
  
}


body{
  padding: 60px;
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 10px;
  
 
}
.card h3 {
  padding:  5px;
  text-align: center;
}
.card img {
  max-height: 100%;
  max-width: 100%;
}
